/* #region header */
.header {
  box-sizing: border-box;

  height: 100vh;
  background-image: url(../../image/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__title {
  margin: 0;
  color: #fff;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
}

.header__bottom {
  padding: 0 20px 40px;
}

/* #endregion */
